@import url("https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css");
.container {
  width: 500px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-top: 100px; }
  .container .button {
    width: 180px;
    height: 20px;
    padding: 10px 20px;
